'use client'; // 声明这是一个客户端组件
import React,{ useEffect, useState }from'react';
import { Text } from '@/components/ui/text';
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from "@/components/ui/dialog";
import { Avatar, AvatarImage, AvatarFallback } from "@/components/ui/avatar";
import { Card, CardContent } from "@/components/ui/card";
import { Input } from '@/components/ui/input';
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { useRouter } from 'next/navigation';
import { useAuth } from '@/context/AuthContext';
import { AiOutlineUser } from "react-icons/ai";
import { BsSearch } from "react-icons/bs";
import ProtectedRoute from '@/components/ProtectedRoute'
import api from '@/lib/api'; // 导入API实例
import '@/app/globals.css';

export default function HomePage() {
  const router = useRouter();
  const { isAuthenticated, logout } = useAuth();
  const [userName, setUsername] = useState('');
  const [loading, setLoading] = useState(true); // 新增loading状态
  const [searchQuery, setSearchQuery] = useState("");

  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [formData, setFormData] = useState({
    name: '',
    institution: '',
    description: '',
    research_area: ''
  });

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = () => {
    console.log('提交的数据:', formData);
    setIsDialogOpen(false);
    // 重置表单
    setFormData({
      name: '',
      institution: '',
      description: '',
      research_area: ''
    });
  };



  // 页面保护：未登录直接跳转
  useEffect(() => {
    if (!isAuthenticated) {
      router.replace('/login'); // 更推荐replace，防止回退
    }
  }, [isAuthenticated, router]);

  // 拉取用户信息
  useEffect(() => {
    const fetchUser = async () => {
      try {
        const response = await api.get('/api/user/me');
        setUsername(response.data.username);
      } catch (error) {
        console.error('获取用户信息失败');
        setUsername('未知用户');
      } finally {
        setLoading(false);
      }
    };
    if (isAuthenticated) {
      fetchUser();
    }
  }, [isAuthenticated]);

  // 页面保护和加载状态
  if (!isAuthenticated || loading) {
    return (
      <div className="flex justify-center items-center h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-gray-900"></div>
      </div>
    );
  }

  return (
    <ProtectedRoute>
      {/* 顶部导航栏 */}
      <header className="flex items-center justify-between px-4 py-3 border-b border-gray-200">
        <div className="flex items-center">
          <div className="w-6 h-6 mr-2 text-black">
            <i className="fas fa-file-alt"></i>
          </div>
          <h1 className="text-lg font-medium text-black">AI+协同文献管理平台</h1>
        </div>
        
        <div className="flex-1 mx-6">
          <div className="relative">
            <Input
              type="text"
              placeholder="搜索文献..."
              className="w-full px-4 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-gray-400 text-sm"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
            />
            <div className="absolute inset-y-0 right-0 flex items-center pr-3 pointer-events-none text-gray-400">
              <BsSearch className="w-4 h-4" />
            </div>
          </div>
        </div>
        
        <div className="flex items-center">
          {/* 用户名按钮，点击跳转到主界面 */}
          <Button
            variant="ghost"
            className="mr-2"
            onClick={() => router.push('/main')}
            title="点击返回主界面"
          >
            {userName}
          </Button>
          {/* 显示登录状态和退出按钮 */}
          {isAuthenticated && (
            <Button variant="outline" size="sm" onClick={logout}>
              退出登录
            </Button>
          )}
        </div>
      </header>
      <div className="flex justify-center bg-white px-4 sm:px-8 relative mt-12">
        <Card className="w-full max-w-none p-6 bg-white shadow-md rounded-lg h-100%">
          <div className="flex items-center mb-4">
            <AiOutlineUser
              className="w-16 h-16 rounded-full mr-4"
            />
            <div>
              <Text as="h2" className="text-3xl font-bold">{userName}</Text>
              <Text as="p" 
                    className="text-xl text-gray-600"
                    onClick={() => router.push('/grouphome')}
                    title="点击进入课题组主页"
              >
                    机器学习课题组
              </Text>
            </div>
          </div>
          <div className="flex justify-around flex-1 mt-8">
            <div className="flex flex-col items-center">
              <Button 
                onClick={() => setIsDialogOpen(true)}
                className="mb-10 -mt-9 text-xl py-6 px-8">创建课题组
              </Button>
            </div>
            <div className="flex flex-col items-center">
              <Button variant="outline" className="mb-10 -mt-9 text-xl py-6 px-8">加入课题组</Button>
            </div>
          </div>
        </Card>
      {/* 创建课题组对话框 */}
      <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
        <DialogContent className="sm:max-w-md">
          <DialogHeader>
            <DialogTitle className="text-xl font-semibold">创建新课题组</DialogTitle>
          </DialogHeader>
          <div className="grid gap-4 py-4">
            <div className="grid grid-cols-4 items-center gap-4">
              <label htmlFor="name" className="text-right font-medium text-gray-700">
                课题组名称
              </label>
              <Input
                id="name"
                name="name"
                value={formData.name}
                onChange={handleInputChange}
                className="col-span-3"
                placeholder="请输入课题组名称"
              />
            </div>
            <div className="grid grid-cols-4 items-center gap-4">
              <label htmlFor="institution" className="text-right font-medium text-gray-700">
                所属机构
              </label>
              <Input
                id="institution"
                name="institution"
                value={formData.institution}
                onChange={handleInputChange}
                className="col-span-3"
                placeholder="请输入所属机构"
              />
            </div>
            <div className="grid grid-cols-4 items-start gap-4">
              <label htmlFor="description" className="text-right font-medium text-gray-700 pt-2">
                课题描述
              </label>
              <Textarea
                id="description"
                name="description"
                value={formData.description}
                onChange={handleInputChange}
                className="col-span-3"
                placeholder="请输入课题描述"
                rows={3}
              />
            </div>
            <div className="grid grid-cols-4 items-start gap-4">
              <label htmlFor="research_area" className="text-right font-medium text-gray-700 pt-2">
                研究方向
              </label>
              <Textarea
                id="research_area"
                name="research_area"
                value={formData.research_area}
                onChange={handleInputChange}
                className="col-span-3"
                placeholder="请输入研究方向"
                rows={3}
              />
            </div>
          </div>
          <DialogFooter className="sm:justify-end">
            <Button
              type="button"
              variant="outline"
              onClick={() => setIsDialogOpen(false)}
              className="!rounded-button whitespace-nowrap cursor-pointer"
            >
              返回
            </Button>
            <Button
              type="button"
              onClick={handleSubmit}
              className="!rounded-button whitespace-nowrap cursor-pointer bg-black hover:bg-gray-800 text-white"
            >
              完成创建
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
      </div>
    </ProtectedRoute>
  );
}